export default class ImageTextures {
  constructor(gl) {
    this.gl = gl;

    this.anisoExt =
      this.gl.getExtension("EXT_texture_filter_anisotropic") ||
      this.gl.getExtension("MOZ_EXT_texture_filter_anisotropic") ||
      this.gl.getExtension("WEBKIT_EXT_texture_filter_anisotropic");

    this.samplers = {};

    const this_ = this;

    /* eslint-disable max-len */
    createImageBitmap(
        this.dataURItoBlob("data:image/jpeg;base64,")
    ).then(function(image) {
      this_.clouds2Image = image;
      this_.samplers.clouds2 = this_.gl.createTexture();
      this_.bindTexture(this_.samplers.clouds2, this_.clouds2Image, 128, 128);
    });
    createImageBitmap(
        this.dataURItoBlob("data:image/gif;base64,R0lGODlhAQABAIAAAAUEBAAAACwAAAAAAQABAAACAkQBADs=")
    ).then(function(image) {
      this_.emptyImage = image;
      this_.samplers.empty = this_.gl.createTexture();
      this_.bindTexture(this_.samplers.empty, this_.emptyImage, 1, 1);
    });
  }

  dataURItoBlob(dataURI) {
    // convert base64 to raw binary data held in a string
    // doesn't handle URLEncoded DataURIs - see SO answer #6850276 for code that does this
    let byteString = atob(dataURI.split(',')[1]);

    // separate out the mime component
    let mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];

    // write the bytes of the string to an ArrayBuffer
    let ab = new ArrayBuffer(byteString.length);

    // create a view into the buffer
    let ia = new Uint8Array(ab);

    // set the bytes of the buffer to the correct values
    for (let i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }

    // write the ArrayBuffer to a blob, and you're done
    let blob = new Blob([ab], {type: mimeString});
    return blob;
  }

  bindTexture(texture, data, width, height) {
    this.gl.bindTexture(this.gl.TEXTURE_2D, texture);

    this.gl.pixelStorei(this.gl.UNPACK_ALIGNMENT, 1);
    this.gl.texImage2D(
      this.gl.TEXTURE_2D,
      0,
      this.gl.RGBA,
      width,
      height,
      0,
      this.gl.RGBA,
      this.gl.UNSIGNED_BYTE,
      data
    );

    this.gl.generateMipmap(this.gl.TEXTURE_2D);

    this.gl.texParameteri(
      this.gl.TEXTURE_2D,
      this.gl.TEXTURE_WRAP_S,
      this.gl.REPEAT
    );
    this.gl.texParameteri(
      this.gl.TEXTURE_2D,
      this.gl.TEXTURE_WRAP_T,
      this.gl.REPEAT
    );
    this.gl.texParameteri(
      this.gl.TEXTURE_2D,
      this.gl.TEXTURE_MIN_FILTER,
      this.gl.LINEAR_MIPMAP_LINEAR
    );
    this.gl.texParameteri(
      this.gl.TEXTURE_2D,
      this.gl.TEXTURE_MAG_FILTER,
      this.gl.LINEAR
    );
    if (this.anisoExt) {
      const max = this.gl.getParameter(
        this.anisoExt.MAX_TEXTURE_MAX_ANISOTROPY_EXT
      );
      this.gl.texParameterf(
        this.gl.TEXTURE_2D,
        this.anisoExt.TEXTURE_MAX_ANISOTROPY_EXT,
        max
      );
    }
  }

  loadExtraImages(imageData) {
    const this_ = this;
    Object.keys(imageData).forEach((imageName) => {
      const { data, width, height } = imageData[imageName];
      if (!this.samplers[imageName]) {
        createImageBitmap(this.dataURItoBlob(data)).then(function(image) {
          this_.samplers[imageName] = this_.gl.createTexture();
          this_.bindTexture(this_.samplers[imageName], image, width, height);
        });
      }
    });
  }

  getTexture(sampler) {
    const tex = this.samplers[sampler];
    if (tex) {
      return tex;
    }

    return this.samplers.clouds2;
  }
}
